import React, { Component } from 'react'
import { injectIntl } from 'react-intl'

import IconEdit from './IconEdit.js'

class ReviewBlock extends Component {
  render() {
    let canEdit = this.props.logged || this.props.step !== 'profile'

    return (
      <div className="mb4 bg-white pa5 relative">
        <div className="fw5 mb4 flex items-center">
          <div className="flex-none">
            {this.props.title}
          </div>
          <div className="flex-none absolute top-0 right-0 pv4 pr4" onClick={() => canEdit ? !this.props.goToStep(this.props.step) : alert('Você precisa se autenticar para editar os dados.')} styles={{'padding': '.2em', 'right': '.4em'}}>
            <IconEdit/>
          </div>
        </div>
        <div className="f6 mb1">
          {this.props.children}
        </div>
      </div>
    )  
  }
}

export default injectIntl(ReviewBlock)
