export function formatPostalCode(number) {
  if (!number) return null
  
  number = number.toString()
  return number.substr(0, 5) + '-' + number.substr(5, 3)
}

export function formatPhone(number) {
  number = number.toString()
  return number.substr(0, number.length - 4) + '-' + number.substr(number.length - 4, 4)
}

export function formatDocument(number) {
  number = number.toString()
  return number.substr(0, 3) + '.' + number.substr(3, 3) + '.' + number.substr(6, 3) + '-' + number.substr(9, 2)
}

export function calculateInterest(principal, frequency, rate) {
  rate = rate / 100
  let cf = rate / (1 - (1 / (Math.pow(1 + rate, frequency))))
  return cf * principal
}

export function protectData(str) {
  let m = '∙'
  
  if (typeof str === 'object') {
    Object.keys(str).forEach(function(key) {
      str[key] = str[key].toString().split("").map(char => Math.random() > 0.3 && char !== ' ' ? m : char).join("");   
    });

    return str
  }
  return str.toString().split("").map(char => Math.random() > 0.3 && char !== ' ' ? m : char).join("");
}