import React, { Component, Fragment } from 'react'
import _ from 'lodash'
import { FormattedMessage, injectIntl } from 'react-intl'
import Button from '@vtex/styleguide/lib/Button'

import MainInstruction from './MainInstruction.js'
import PaymentOption from './PaymentOption.js'
import PaymentCardForm from './PaymentCardForm.js'
import PaymentExtraInfo from './PaymentExtraInfo.js'

class StepPayment extends Component {
  constructor() {
    super()

    this.state = {
      typedCardNumber: '',
      highlightedOption: null,
    }
  }

  handleSelectOption = (payment) => {
    this.setState({
			highlightedOption: payment,
		})

    let time1 = setTimeout(() => {
      this.props.selectPayment(payment)
    }, 200)


    let time2 = setTimeout(() => {
      if (this.props.step === 'review') {
        if (payment.name === 'Boleto') {
          this.props.goToStep('review')
        }
      } else {
        if (payment.name === 'Boleto') {
          this.props.goToNextStep()
        }
      }
    }, 1000)

    let time3 = setTimeout(() => {
      this.setState({
        highlightedOption: null,
      })
    }, 1300)
  }

  handleChangeTypedNumber = (number) => {
		this.setState({
			typedCardNumber: number
		})
  }

  render() {
    let {
      step,
      address,
      payments,
      selectedPayment,
      cardData,
      savedCardData,
      returningShopper,
      selectPayment,
      removeSelectedPayment,
      setCardData,
      paymentPage,
      totals,
      selectedInstallment,
      setInstallment,
      setPaymentPage,
      modalOpen,
    } = this.props

    let {
      highlightedOption
    } = this.state

    let payment = selectedPayment.length > 0 && selectedPayment[0].name
    let showEditOptions = payment === 'CreditCard' && paymentPage !== 'cardForm'

    let allowNextStep = false

    if (
      (payment === 'CreditCard' && paymentPage !== 'cardForm') ||
      (payment === 'Boleto')
    ) {
      allowNextStep = true
    }

    let savedPayments = _.filter(payments, payment => payment.saved)
    let defaultPayments = _.filter(payments, payment => !payment.saved)

    return (
      <Fragment>
        <div className="mt5 lh-title">
          <div
            className={`ph5 f5 fw5 sla-transition ${highlightedOption || selectedPayment.length > 0 ? 'pt4 pb5' : ''}`}
            style={{
              maxHeight: highlightedOption || selectedPayment.length > 0 ? '120px' : '0'
            }}
          >
              <FormattedMessage id="checkout.youHaveSelected"/>
          </div>

          {
            selectedPayment.length > 0 && (

              // Selected payments
              <Fragment>  
                {
                  selectedPayment.map(payment => {
                    return (
                      <PaymentOption
                        isSelected={true}
                        name={payment.name}
                        saved={payment.saved}
                        id={payment.id}
                        totals={totals}
                        cardNumber={payment.saved ? savedCardData.number.toString() : this.state.typedCardNumber || cardData.number.toString()}
                        showEditOptions={showEditOptions}
                        selectedInstallment={selectedInstallment}
                        removeSelectedPayment={removeSelectedPayment}
                        modalOpen={modalOpen}
                        setPaymentPage={setPaymentPage}
                      />
                    )
                  })
                }
              </Fragment>
            )
          }

          {
            selectedPayment.length === 0 && (
              <Fragment>
                <div className={`mh5 mb4 sla-transition ${highlightedOption ? 'o-10' : ''}`}>
                  <MainInstruction><FormattedMessage id="checkout.selectAPaymentMethod"/></MainInstruction>
                </div>

                {
                  returningShopper && savedPayments && (

                    // Saved cards
                    <Fragment>
                      <div className="pb5 pt2">
                        <div className="ph5 sla-transition">
                          <h3 className="ttu c-muted-1 f6 fw5 mb3"><FormattedMessage id="checkout.yourCards"/></h3>
                        </div>

                        {
                          savedPayments.map(payment => {
                            return (
                              <div className="sla-transition" onClick={() => selectPayment(payment)}>
                                <PaymentOption
                                  name={payment.name}
                                  saved={payment.saved}
                                  cardData={payment.saved ? savedCardData : cardData}
                                  cardNumber={payment.saved ? savedCardData.number.toString() : this.state.typedCardNumber || cardData.number.toString()}
                                  id={payment.id}
                                  installments={payment.installments}
                                />
                              </div>
                            )
                          })
                        }
                      </div>
                      <div className={`mh5 sla-transition ${highlightedOption ? 'o-10' : ''}`}>
                        <h3 className="ttu c-muted-1 f6 fw5 mb3"><FormattedMessage id="checkout.otherOptions"/></h3>
                      </div>
                    </Fragment>
                  )
                }

                {
                  defaultPayments.map(payment => {
                    return (

                      // List of payments
                      <div
                        className={
                          `sla-transition
                          ${payment === highlightedOption ? 'bg-muted-5' : ''}
                          ${highlightedOption && payment !== highlightedOption ? 'o-10' : ''}`
                        }
                        style={{
                          maxHeight: highlightedOption && payment !== highlightedOption ? '0' : ''
                        }}
                        onClick={() => this.handleSelectOption(payment)}
                      >
                        <PaymentOption
                          name={payment.name}
                          cardData={cardData}
                          saved={payment.saved}
                          id={payment.id}
                          installments={payment.installments}
                        />
                      </div>
                    )
                  })
                }
              </Fragment>
            )
          }

        </div>

        {
          payment === 'CreditCard' && paymentPage === 'cardForm' && (
            <Fragment>
              <PaymentCardForm
                step={step}
                address={address}
                cardData={cardData}
                setCardData={setCardData}
                setInstallment={setInstallment}
                setPaymentPage={setPaymentPage}
                installments={selectedPayment[0].installments}
                saved={selectedPayment[0].saved}
                changeTypedNumber={this.handleChangeTypedNumber}
                modalOpen={modalOpen}
                goToStep={this.props.goToStep}
                goToNextStep={this.props.goToNextStep}
                />
            </Fragment>
          )
        }
        {
          false && payment === 'CreditCard' && paymentPage === 'last' && !selectedPayment[0].saved && (
            <PaymentExtraInfo
              cardData={cardData}
              setCardData={setCardData}
              address={address}
            />
          )
        }

        {
          allowNextStep && !highlightedOption && (
            <div className="flex-none pa5 mt5 w-100 bg-white">
              {
                step === 'review' ? (
                  <Button size="large" block onClick={() => this.props.goToStep('review')}><FormattedMessage id="checkout.confirmAndReview" /></Button>
                ) : (
                  <Button size="large" block onClick={this.props.goToNextStep}><FormattedMessage id="checkout.reviewOrder" /></Button>
                )
              }

              
            </div>
          )
        }
      </Fragment>
    )
  }
}

export default injectIntl(StepPayment)
