import React, { Component } from 'react'
import { FormattedMessage, injectIntl } from 'react-intl'
import { Link, useHistory } from 'react-router-dom'

import Button from '@vtex/styleguide/lib/Button'
import Input from '@vtex/styleguide/lib/Input'
import Spinner from '@vtex/styleguide/lib/Spinner'
import ButtonPlain from '@vtex/styleguide/lib/ButtonPlain'

import EmailBullet from './EmailBullet.js'

class StepProfile extends Component {
  constructor() {
    super()

    this.state = {
      loading: false,
      email: '',
      name: '',
      phoneNumber: '',
      document: '',
    }
  }

  componentDidMount() {
    this.setState({
      email: this.props.profile.email,
      name: this.props.profile.name,
      phoneNumber: this.props.profile.phoneNumber,
      document: this.props.profile.document,
		})
  }

  handleUpdateData = (key, value) =>{
    this.setState({
			[key]: value,
		})
  }

  handleVerifyEmail = (e) => {
    e.preventDefault();
    this.setState({
			loading: true,
		})

    setTimeout(() => {
      this.setState({loading: false})

      this.props.setProfile({
        email: this.state.email,
      })

      if (this.props.shortcutFlow) {
        this.props.goToStep('shipping')
      }  
    }, 900)
  }

  handleResetData = () => {
    this.props.setProfile({
      email: '',
      name: '',
      phoneNumber: '',
      document: '',
    })
  }

  handleSubmitData = (e) => {
    e.preventDefault();

    this.props.setProfile({
      email: this.state.email,
      name: this.state.name,
      phoneNumber: this.state.phoneNumber,
      document: this.state.document,
    })
    
    this.props.goToNextStep()
  }

  render() {
    let {
      step,
      profile,
      returningShopper,
      goToStep,
      intl: { formatMessage, },
    } = this.props

    let {
      loading,
      name,
      phoneNumber,
      document,
    } = this.state

    if (!profile.email) {
      return (
        <form onSubmit={(e) => this.handleVerifyEmail(e)}>
          <div className="mt5 ph5">
            <div className="mb5">
              <Input type="email" size="large" label={formatMessage({id: 'checkout.email'})} onChange={(e) => { this.handleUpdateData('email', e.target.value)}} />
            </div>
            <div className="flex-none w-100 bg-white mb3">
              <Button type="submit" size="large" block>
                {loading ? <Spinner color="#fff" size={20} /> : <FormattedMessage id="checkout.verifyEmail" />}
              </Button>
            </div>
            <div className="mt7 bt b--muted-4 pt6 c-muted-1">
              <div className="mb4 fw6">
                <FormattedMessage id="checkout.youWillBeAbleTo" />
              </div>
              <EmailBullet><FormattedMessage id="checkout.useAddressAndCards" /></EmailBullet>
              <EmailBullet><FormattedMessage id="checkout.useRegisteredGiftCards" /></EmailBullet>
              <EmailBullet><FormattedMessage id="checkout.receiveNotifications" /></EmailBullet>
            </div>
          </div>
        </form>
      )
    }

    return (
      <form onSubmit={(e) => this.handleSubmitData(e)}>
        <div className="mt2 ph5">
          <div className="mb6 flex items-center">
            <div className="flex-none">
              {profile.email}
            </div>
            <div className="flex-none ml3">
              <ButtonPlain onClick={this.handleResetData}><span className="fw4"><FormattedMessage id="checkout.change"/></span></ButtonPlain>
            </div>
          </div>
          <div className="mb5">
            <Input size="large" value={name} label={formatMessage({id: 'checkout.name'})} onChange={(e) => { this.handleUpdateData('name', e.target.value)}} />
          </div>
          <div className="mb5">
            <Input type="tel" size="large" value={phoneNumber} minLength="11" maxLength="11" label={formatMessage({id: 'checkout.phoneNumber'})} onChange={(e) => { this.handleUpdateData('phoneNumber', e.target.value)}} />
          </div>
          <div className="mb5">
            <Input type="tel" size="large" value={document} minLength="11" maxLength="11" label={formatMessage({id: 'checkout.document'})} onChange={(e) => { this.handleUpdateData('document', e.target.value)}} />
          </div>
        </div>
        <div className="flex-none pa5 w-100 bg-white">
          {
            step === 'review' ? (
              <Button size="large" block onClick={() => goToStep('review')}><FormattedMessage id={`checkout.confirmAndReview`}/></Button>
            ) : (
              <Button type="submit" size="large" block><FormattedMessage id="checkout.goToShipping" /></Button>
            )
          }
        </div>
      </form>
    )
  }
}

export default injectIntl(StepProfile)
