import React from 'react'
import { BrowserRouter, Switch, Route, Link, useLocation } from 'react-router-dom'
import { useTransition, animated } from 'react-spring'
import _ from 'lodash'
import './styles.css'

export default function StepTransition(p) {
  const location = useLocation()

  let goBack = true

  if (p.nextStep !== p.currentStep) {
    let actualSteps = _.filter(p.steps, step => step.available)
    let nextStepIndex = _.findIndex(actualSteps, item => item.name === p.nextStep)
    let currentStepIndex = _.findIndex(actualSteps, item => item.name === p.currentStep)
    goBack = currentStepIndex > nextStepIndex
  }

  const transitions = useTransition(location, location => location.pathname, {
    unique: true,
    from: { transform: `translate3d(${goBack ? '-' : ''}150%,0,0)`, position: 'absolute' },
    enter: { transform: 'translate3d(0%,0,0)', position: 'relative' },
    leave: { transform: `translate3d(${goBack ? '' : '-'}150%,0,0)`, position: 'absolute' },
  })


  return transitions.map(({ item: location, props, key }) => (
    <animated.div key={key} style={props}>
      <Switch location={location}>
        <Route path="/" exact>{p.cart}</Route>
        <Route path="/cart">{p.cart}</Route>
        <Route path="/profile">{p.profile}</Route>
        <Route path="/shipping">{p.shipping}</Route>
        <Route path="/address">{p.address}</Route>
        <Route path="/payment">{p.payment}</Route>
        <Route path="/review">{p.review}</Route>
        <Route path="/confirmation">{p.confirmation}</Route>
      </Switch>
    </animated.div>
  ))
}