import React, { Component } from 'react'
import { FormattedMessage, injectIntl } from 'react-intl'

import Button from '@vtex/styleguide/lib/Button'
import Input from '@vtex/styleguide/lib/Input'

import { formatPostalCode } from '../utils.js'
import AddressList from './AddressList.js'

class StepAddress extends Component {
  constructor() {
    super()
    this.state = {
      street: '',
      number: '',
      neighbourhood: '',
      city: '',
      state: '',
      receiverName: '',
      isSelected: true,
    }
  }

  componentDidMount() {
    this.setState({
      street: this.props.address.street,
      number: this.props.address.number,
      neighbourhood: this.props.address.neighbourhood,
      city: this.props.address.city,
      state: this.props.address.state,
      receiverName: this.props.address.receiverName,
		})
  }

  handleUpdateData = (key, value) =>{
    this.setState({
			[key]: value,
		})
  }

  handleSubmitData = (e) => {
    e.preventDefault();

    this.props.setAddress({
      number: this.state.number,
      receiverName: this.state.receiverName,
    })

    if (this.props.step === 'review')
      this.props.goToStep('review')
    else
      this.props.goToNextStep()
  }

  handleSelect = () => {
    this.setState({
			isSelected: true,
		})

    if (this.props.step === 'review')
      this.props.goToStep('review')
    else
      this.props.goToStep('shipping')
  }

  handleRemoveSelected = () => {
    this.setState({
			isSelected: false,
		})
  }

  render() {
    let {
      step,
      address,
      addressList,
      returningShopper,
      location,
      goToStep,
      payments,
      intl: { formatMessage, },
    } = this.props

    let {
      street,
      number,
      neighbourhood,
      city,
      state,
      receiverName,
    } = this.state

    if (false && addressList && addressList.length > 0) {
      return (
        <AddressList
          address={address}
          list={addressList}
          location={location}
          goToStep={goToStep}
          select={this.handleSelect}
        />
      )
    }

    return (
      <form onSubmit={(e) => this.handleSubmitData(e)}>
        <div className="mt5 ph5">
          <div className="mb6 lh-title">
            <div>{street}</div>
            <div>{neighbourhood}</div>
            <div>{city} - {state}</div>
            <div>{formatPostalCode(location)}</div>
          </div>
          <div className="mb5 w4">
            <Input type="tel" size="large" value={number} label={formatMessage({id: 'checkout.number'})} onChange={(e) => { this.handleUpdateData('number', e.target.value)}} />
          </div>
          <div className="mb5">
            <Input size="large" value={receiverName} label={formatMessage({id: 'checkout.receiverName'})} onChange={(e) => { this.handleUpdateData('receiverName', e.target.value)}} />
          </div>
        </div>
        <div className="flex-none pa5 w-100 bg-white">
          {
            step === 'review' || payments.length > 0 ? (
              <Button type="submit" size="large" block><FormattedMessage id="checkout.confirmAndReview" /></Button>
            ) : (
              <Button type="submit" size="large" block><FormattedMessage id="checkout.goToPayment" /></Button>
            )
          }
        </div>
      </form>
    )
  }
}

export default injectIntl(StepAddress)
