import { Fragment } from "react"
import Button from '@vtex/styleguide/lib/Button'

const UsabilityTour = props => {
  let task = props.usabilityTourTask
  let showTour = task === '1' || task === '1done' || task === '2' || task === '2done' || task === '3' || task === '3done'

  return (
    <Fragment>
      {
        showTour && (
          <div className="fixed absolute--fill c-on-base z-999 flex">
            <div className="fixed absolute--fill bg-base--inverted o-90"></div>

            <div className="relative c-on-base--inverted mv7 mh6 f4">
              {
                props.usabilityTourTask === '1' && (
                  <div>
                    <h3>Tarefa 1</h3>
                    <p>
                      Você decidiu comprar um produto que está precisando com grande urgência. Considere que você já escolheu o produto e o adicionou ao seu carrinho. 
                    </p>
                    <p>
                    Sua tarefa é finalizar a compra deste produto utilizando o cartão de crédito que te fornecemos.
                    </p>
                    <p className="mt6">
                      <Button variation="secondary" onClick={() => props.taskRedirect('1doing')}>Iniciar tarefa</Button>
                    </p>
                  </div>
                )
              }

              {
                props.usabilityTourTask === '1done' && (
                  <div>
                    <h3>Fim da Tarefa 1!</h3>
                    <p>
                      A primeira tarefa chegou ao fim.
                    </p>
                    <p className="mt6">
                      <Button variation="secondary" onClick={() => props.taskRedirect('2')}>Ver próxima tarefa</Button>
                    </p>
                  </div>
                )
              }

              {
                props.usabilityTourTask === '2' && (
                  <div>
                    <h3>Tarefa 2</h3>
                    <p>
                      Depois do sucesso da sua primeira compra, você resolveu voltar à mesma loja comprar mais alguns produtos. 
                    </p>
                    <p>
                      Sua tarefa é realizar uma nova compra com esses produtos. Sua preferência neste caso é retirar o máximo de produtos possíveis em uma loja e o pagamento e pagar com cartão de crédito.
                    </p>
                    <p className="mt6">
                      <Button variation="secondary" onClick={() => props.taskRedirect('2doing')}>Iniciar tarefa</Button>
                    </p>
                  </div>
                )
              }

              {
                props.usabilityTourTask === '2done' && (
                  <div>
                    <h3>Fim da Tarefa 2!</h3>
                    <p>
                      A segunda tarefa chegou ao fim.
                    </p>
                    <p className="mt6">
                      <Button variation="secondary" onClick={() => props.taskRedirect('3')}>Ver próxima tarefa</Button>
                    </p>
                  </div>
                )
              }

              {
                props.usabilityTourTask === '3' && (
                  <div>
                    <h3>Tarefa 3</h3>
                    <p>
                      Você decidiu fazer uma terceira compra com os mesmos produtos.  
                    </p>
                    <p>
                      Sua tarefa dessa vez é finalizar essa compra escolhendo a forma de entrega que desejar, porém fazendo o pagamento com boleto bancário.
                    </p>
                    <p className="mt6">
                      <Button variation="secondary" onClick={() => props.taskRedirect('3doing')}>Iniciar tarefa</Button>
                    </p>
                  </div>
                )
              }

              {
                props.usabilityTourTask === '3done' && (
                  <div>
                    <h3>Fim da Tarefa 3!</h3>
                    <p>
                      Seu teste chegou ao fim.
                    </p>
                  </div>
                )
              }


            </div>
          </div>
        )
      }
    </Fragment>
  )
}

export default UsabilityTour
