import React, { Component } from 'react'
import { FormattedMessage, injectIntl } from 'react-intl'

import MainInstruction from './MainInstruction.js'

import InputButton from '@vtex/styleguide/lib/InputButton'

class Location extends Component {
  constructor() {
    super()

		this.state = {
      typedAddress: '',
    }
  }

  handleChangeAddress = (e) => {
    this.setState({typedAddress: e.target.value})
  }

  render() {
    let {
      intl: { formatMessage, }
    } = this.props

    return (
      <div className="mt5">
        <div className="mh5 mb3">
          <MainInstruction><FormattedMessage id="checkout.typeYourPostalCode"/></MainInstruction>
        </div>
        <div className="ph5 mt5 pt2" style={{'width': '310px'}}>
          <form onSubmit={(e) => this.props.loadOptions(this.state.typedAddress, e)}>
            <InputButton type="tel" minLength="8" maxLength="8" buttonProps={{'variation': 'primary'}} size="large" label={formatMessage({id: 'checkout.yourAddress'})} button="OK" onChange={this.handleChangeAddress} />
          </form>
        </div>
      </div>
    )
  }
}

export default injectIntl(Location)
