import React, { Component } from 'react'
import { FormattedMessage, injectIntl } from 'react-intl'

import Input from '@vtex/styleguide/lib/Input'
import ButtonPlain from '@vtex/styleguide/lib/ButtonPlain'

class PaymentCardExtraInfo extends Component {
  constructor() {
    super()
    this.state = {
      document: '',
    }
  }

  componentDidMount() {
    this.setState({
      document: this.props.cardData.document,
		})
  }

  handleUpdateData = (key, value) =>{
    this.setState({
			[key]: value,
		})
  }

  handleSubmitData = (e) => {
    e && e.preventDefault();

    this.props.setCardData({
      document: this.state.document,
    })
  }

  render() {
    let {
      address,
      intl: { formatMessage, },
    } = this.props

    let {
      document,
    } = this.state

    return (
      <div>
        <div className="pa5 mh5 bg-muted-5 mt1">
          <div className="mb5 lh-title">
            <FormattedMessage id="checkout.reviewExtraData"/>
          </div>
          <div className="mb6 w5">
            <Input type="tel" size="large" value={document} minLength="11" maxLength="11" label={formatMessage({id: 'checkout.ownerDocument'})} onChange={(e) => { this.handleUpdateData('document', e.target.value)}} />
          </div>
          <div>
            <div className="flex items-center fw5 mb4">
              <div className="flex-none mr3">
                <FormattedMessage id="checkout.billingAddress"/>
              </div>
              <div className="flex-none">
                <ButtonPlain><span className="fw4"><FormattedMessage id="checkout.change"/></span></ButtonPlain>
              </div>
            </div>
            <div className="mb3">
              <div>{address.street}, {address.number ? address.number : 12}</div>
              <div>{address.neighbourhood}</div>
              <div>{address.city} - {address.state}</div>
            </div>
          </div>
        </div>
      </div>
    )
  }
}

export default injectIntl(PaymentCardExtraInfo)
