import React, { Component, Fragment } from 'react'
import { FormattedMessage, injectIntl } from 'react-intl'
import _, { over } from 'lodash'

import SlaOption from './SlaOption.js'
import ItemsPreview from './ItemsPreview.js'
import Location from './Location.js'
import SlaOptionSkeleton from './SlaOptionSkeleton.js'
import GroupTitle from './GroupTitle.js'
import AddressList from './AddressList.js'

import IconTrash from './IconTrash.js'

import Button from '@vtex/styleguide/lib/Button'
import ButtonPlain from '@vtex/styleguide/lib/ButtonPlain'

import { formatPostalCode } from '../utils.js'
import { currencyOptions } from '../data/const.js'

class StepShipping extends Component {
  constructor() {
    super()
    
    this.state = {
      loading: false,
      showAlert: true,
      animateSla: null,
      highlightedSla: null,
      showList: true,
    }
  }
  
  handleChangeLocation = () => {
    this.handleHideAlert()
    this.props.setLocation('') 
  }

  handleBackToAddressList = () => {
    this.setState({
      showList: true,
    })
  }

  handleHideAlert = () => {
    this.props.hideReturningAlert()
  }

  handleLoadOptions = (location, e) => {
    e && e.preventDefault()

    this.setState({
			loading: true,
      showList: true,
		}, () => {
      this.props.setLocation(location)
      this.props.removeAllSla()
    })

    this.timeout = setTimeout(() => {
      this.setState({
        loading: false,
      })
    }, 1200)
  }

  handleReviewAndDisableShortcut = () => {
    this.props.goToStep('review')
    // this.props.disableShortcut()
  }

  handleSelectSla = (sla) => {
    this.handleHideAlert()
    this.props.selectSla(sla)
  }

  handleShowPickup = (pickup) => {
    this.props.showPickup(pickup)
    this.handleHideAlert()

    if (this.props.step === 'review') this.props.modalSetPageBack('shipping')
  }

  handleSelectAddress = (address) => {
    if (!address) {
      this.setState({
        showList: false,
      })
    } else {
      this.handleLoadOptions(address.location)
    }
  }

  render() {
    let {
      step,
      location,
      address,
      addressList,
      slas,
      selectedSla,
      formatData,
      products,
      remainingItems,
      hasAddressStep,
      totals,
      returningShopper,
      removeSelectedSla,
      setShowingItemsList,
      showPickup,
      modalOpen,
      goToStep,
      goToNextStep,
      highlightedSla,
      payments,
      intl: { formatMessage, formatNumber }
    } = this.props

    let selectedShippingSlas = _.filter(selectedSla, (sla) => {
      return sla.channel === 'delivery'
    })

    let selectedPickupSlas = _.filter(selectedSla, (sla) => {
      return sla.channel === 'pickup-in-point'
    })

    let remainingShippingSlas = _.filter(slas, (sla) => {
      return sla.channel === 'delivery' && sla.remainingItems && sla.remainingItems.length > 0
    })

    let remainingPickupSlas = _.filter(slas, (sla) => {
      return sla.channel === 'pickup-in-point' && sla.remainingItems && sla.remainingItems.length > 0
    })

    if (!location && this.state.showList && addressList.length > 0) {
      return (
        <AddressList
          address={address}
          list={addressList}
          goToStep={goToStep}
          select={this.handleSelectAddress}
        />
      )
    }

    if (!location) {
      return (
        <div>
          <Location
            location={location}
            loadOptions={this.handleLoadOptions}
          />

          {
            this.props.addressList && this.props.addressList.length > 0 && (
              <div className="ma5 pt2">
                <ButtonPlain onClick={this.handleBackToAddressList}>
                  <FormattedMessage id="checkout.backToAddressList" />
                </ButtonPlain>
              </div>
            )
          }
        </div>
      )
    }

    let locationInfo = (
      <div className="ph5 mb5">
        <div className="flex mt4">
            <div className="flex-auto items-center">
              <div className="flex items-center">
                <div className="flex-none mr2">
                  <FormattedMessage id="checkout.optionsFor"/>
                </div>
                <div className="flex-none mr2">
                  {formatMessage({id: 'checkout.yourAddress'})}
                </div>
                <div className="flex-none mr3">
                  {formatPostalCode(location)}
                </div>
                <div className="flex-none">
                  <ButtonPlain onClick={this.handleChangeLocation}><span className="fw4"><FormattedMessage id="checkout.change"/></span></ButtonPlain>
                </div>
              </div>
            </div>
        </div>
      </div>
    )

    if (this.state.loading) {
      return (
        <div>
          {locationInfo}
          <SlaOptionSkeleton/>
          <SlaOptionSkeleton/>
          <SlaOptionSkeleton/>
        </div>
      )
    }

    return (
      <Fragment>
        {/* loading: {this.state.loading ? 'sim' : 'não'} */}
        {locationInfo}

        {/* Selected items */}

        <div
          className={`ph5 f5 fw5 sla-transition ${highlightedSla || selectedSla.length > 0 ? 'pt4' : ''}`}
          style={{
            maxHeight: highlightedSla || selectedSla.length > 0 ? '120px' : '0'
          }}
        >
            <FormattedMessage id="checkout.youHaveSelected"/>
        </div>

        {
          selectedSla.length > 0 && (
            <div>
              {
                selectedPickupSlas.length > 0 && (
                  <div className="ph5 pb2">
                    <GroupTitle><FormattedMessage id="checkout.pickupInStore"/></GroupTitle>
                  </div>
                )
              }

              {selectedPickupSlas.map((item, i) => {
                  return (
                    <div key={i} className="mb3">
                      {
                        <SlaOption
                          isSelected={true}
                          name={item.name}
                          id={item.id}
                          time={formatData(item.sla, item.timeType, item.channel)}
                          channel={item.channel}
                          price={item.lowPrice ? item.lowPrice : item.price}
                          products={products}
                          hasManySlasSelected={selectedSla.length > 1}
                          availableItems={item.availableItems}
                          selectedItems={item.selectedItems}
                          removeSelectedSla={removeSelectedSla}
                          setShowingItemsList={setShowingItemsList}
                          showPickup={showPickup}
                          pickup={item}
                        />
                      }
                    </div>
                  )
              })}

              {
                selectedShippingSlas.length > 0 && (
                  <div className="ph5 pb2">
                    <GroupTitle><FormattedMessage id="checkout.shipToHome"/></GroupTitle>
                  </div>
                )
              }

              {selectedShippingSlas.map((item, i) => {
                  return (
                    <div key={i} className={`${i < selectedShippingSlas.length - 1 ? 'mb3' : 'mb2'}`}>
                      {
                        <SlaOption
                          isSelected={true}
                          name={item.name}
                          id={item.id}
                          time={formatData(item.sla, item.timeType, item.channel)}
                          channel={item.channel}
                          price={item.lowPrice ? item.lowPrice : item.price}
                          products={products}
                          hasManySlasSelected={selectedSla.length > 1}
                          availableItems={item.availableItems}
                          selectedItems={item.selectedItems}
                          removeSelectedSla={removeSelectedSla}
                          setShowingItemsList={setShowingItemsList}
                          showPickup={showPickup}
                          pickup={item}
                        />
                      }
                    </div>
                  )
              })}
            </div>
          )
        }


        {/* Remaining title */}
        {
          selectedSla.length > 0 && remainingItems.length > 0 && (
            <div className="mh5 mt4">
              <div className="mb3 mt6 f5 fw5"><FormattedMessage id="checkout.remainingItems"/></div>
              <div className="c-muted-1">
                {
                  remainingPickupSlas.length === 1 ? (
                    <FormattedMessage id="checkout.remainingItemsHelp"/>
                  ) :
                  (
                    <FormattedMessage id="checkout.remainingItemsHelpPlural"/>
                  )
                }
              </div>

              <div className="mv4">
                <ItemsPreview
                  items={remainingItems}
                  setShowingItemsList={setShowingItemsList}
                />
              </div>
            </div>
          )
        }
       
        {/* Remaining items */}
        {
          remainingItems.length > 0 && (
            <Fragment>

              {/* Shipping */}
              <div className="">
                <div className={`ph5 sla-transition`}>
                  <GroupTitle><FormattedMessage id="checkout.shipToHome"/></GroupTitle>
                </div>

                {
                  remainingShippingSlas.map((item, i) => {
                    return (
                      <Fragment key={i}>
                        {
                          <div
                            onClick={() => this.handleSelectSla(item)}
                            className={
                              `sla-transition
                              ${item === highlightedSla ? 'bg-muted-5 mh5' : ''}
                              ${highlightedSla && item !== highlightedSla ? 'o-10' : ''}`
                            }
                            style={{
                              maxHeight: highlightedSla && item !== highlightedSla ? '0' : ''
                            }}
                          >
                            <SlaOption
                              name={item.name}
                              time={formatData(item.sla, item.timeType, item.channel)}
                              channel={item.channel}
                              price={item.lowPrice ? item.lowPrice : item.price}
                              products={products}
                              remainingItems={remainingItems}
                              slaRemainingItems={item.remainingItems}
                              availableItems={item.availableItems}
                              unavailableItems={item.unavailableItemsFull.length}
                            />
                          </div>
                        }
                      </Fragment>
                    )
                })}
              </div>

              {/* Pickup */}
              {
                remainingPickupSlas.length > 0 && (
                  <div className="mt5">
                    <div className={`ph5 sla-transition ${highlightedSla ? 'o-10' : ''}`}>
                      <GroupTitle><FormattedMessage id="checkout.pickupInStore"/></GroupTitle>
                    </div>

                    {
                      remainingPickupSlas.map((item, i) => {
                        return (
                          <Fragment key={i}>
                          {
                            i === 0 &&
                              <div className={
                                  `sla-transition
                                  ${item === highlightedSla ? 'bg-muted-5 mh5' : ''}
                                  ${highlightedSla && item !== highlightedSla ? 'o-10' : ''}`
                                } onClick={() => this.handleShowPickup(item)}
                              >
                                <SlaOption
                                  name={item.name}
                                  time={formatData(item.sla, item.timeType, item.channel)}
                                  channel={item.channel}
                                  distance={item.distance}
                                  price={item.lowPrice ? item.lowPrice : item.price}
                                  products={products}
                                  remainingItems={remainingItems}
                                  slaRemainingItems={item.remainingItems}
                                  availableItems={item.availableItems}
                                  unavailableItems={item.unavailableItemsFull.length}
                                />
                              </div>
                          }
                          </Fragment>
                        )
                    })}

                    <div className={`flex-auto ph5 pt4 c-on-base fw5 sla-transition ${highlightedSla ? 'o-10' : ''}`} onClick={() => modalOpen('pickupList')}>
                      <div className="flex items-center">
                        <div className="flex-auto"><FormattedMessage id="checkout.viewOtherStores"/></div>

                        <div className="flex-none ml3 mt0">
                          <div className="flex items-center justify-end" style={{height: '26px', width: '26px'}}>
                            <svg width="16" height="12" viewBox="0 0 9 12" fill="none" xmlns="http://www.w3.org/2000/svg">
                              <path d="M8.32251 5.28071L2.29701 0.280712C2.06973 0.0985538 1.76532 -0.00224062 1.44934 3.78026e-05C1.13337 0.00231622 0.831116 0.107485 0.60768 0.292893C0.384245 0.478301 0.257506 0.729114 0.25476 0.991311C0.252014 1.25351 0.373482 1.50611 0.593 1.69471L5.7665 5.98771L0.593 10.2807C0.477901 10.373 0.386094 10.4833 0.322936 10.6053C0.259778 10.7273 0.226534 10.8585 0.225143 10.9913C0.223753 11.1241 0.254244 11.2558 0.314837 11.3787C0.375431 11.5016 0.464913 11.6132 0.578063 11.7071C0.691213 11.801 0.825765 11.8753 0.973868 11.9255C1.12197 11.9758 1.28066 12.0011 1.44067 12C1.60068 11.9988 1.75881 11.9712 1.90584 11.9188C2.05287 11.8664 2.18585 11.7902 2.29701 11.6947L8.32251 6.69471C8.54843 6.50718 8.67535 6.25288 8.67535 5.98771C8.67535 5.72255 8.54843 5.46824 8.32251 5.28071Z" fill="#134CD8"/>
                            </svg>
                          </div>
                        </div>
                      </div>
                      <div className="mt5 bb b--muted-4"></div>
                    </div>

                  </div>
                )
              }

              {/* Remove from cart */}
              {
                selectedSla.length > 0 && remainingItems.length > 0 && (
                  <div className="mt5">
                    <div className="ph5">
                      <GroupTitle><FormattedMessage id="checkout.otherOptions"/></GroupTitle>
                    </div>

                    <div className="flex-auto ph5 pt4 c-muted-2 fw5" onClick={() => this.props.promptOpen('removeItems')}>
                      <div className="flex items-center">
                        <div className="flex-none mr3"><IconTrash /></div>

                        <div className="flex-auto"><FormattedMessage id="checkout.removeRemainingItems"/></div>

                        <div className="flex-none ml3 mt0">
                          <div className="flex items-center justify-end" style={{height: '26px', width: '26px'}}>
                            <svg width="16" height="12" viewBox="0 0 9 12" fill="none" xmlns="http://www.w3.org/2000/svg">
                              <path d="M8.32251 5.28071L2.29701 0.280712C2.06973 0.0985538 1.76532 -0.00224062 1.44934 3.78026e-05C1.13337 0.00231622 0.831116 0.107485 0.60768 0.292893C0.384245 0.478301 0.257506 0.729114 0.25476 0.991311C0.252014 1.25351 0.373482 1.50611 0.593 1.69471L5.7665 5.98771L0.593 10.2807C0.477901 10.373 0.386094 10.4833 0.322936 10.6053C0.259778 10.7273 0.226534 10.8585 0.225143 10.9913C0.223753 11.1241 0.254244 11.2558 0.314837 11.3787C0.375431 11.5016 0.464913 11.6132 0.578063 11.7071C0.691213 11.801 0.825765 11.8753 0.973868 11.9255C1.12197 11.9758 1.28066 12.0011 1.44067 12C1.60068 11.9988 1.75881 11.9712 1.90584 11.9188C2.05287 11.8664 2.18585 11.7902 2.29701 11.6947L8.32251 6.69471C8.54843 6.50718 8.67535 6.25288 8.67535 5.98771C8.67535 5.72255 8.54843 5.46824 8.32251 5.28071Z" fill="#134CD8"/>
                            </svg>
                          </div>
                        </div>
                      </div>
                      <div className="mt5 bb b--muted-4"></div>
                    </div>
                  </div>
                )
              }
            </Fragment>
          )
        }

        {
          (!highlightedSla && remainingItems.length === 0) &&
          <div className="mh5">
            {
              selectedSla.length > 1 &&
              <div className="pt3 pb5 fw5 tr">
                <FormattedMessage id="checkout.total"/>: { formatNumber(totals.shipping, currencyOptions) }
              </div>
            }
            <div className="mt5 pt5">
              {
                step === 'review' || (returningShopper && payments.length > 0 && address.number) ? (
                  <Button size="large" block onClick={this.handleReviewAndDisableShortcut}><FormattedMessage id={`checkout.confirmAndReview`}/></Button>
                ) : (
                  <Button size="large" block onClick={goToNextStep}><FormattedMessage id={`checkout.${hasAddressStep ? 'complementAddress' : 'goToPayment'}`}/></Button>
                )
              }
            </div>
          </div>
        }
      </Fragment>
    )
  }
}

export default injectIntl(StepShipping)
