import React, { Component, Fragment } from 'react'
import { FormattedMessage, injectIntl } from 'react-intl'

import IconArrow from './IconArrow.js'
import IconEdit from './IconEdit.js'
import IconCard from './IconCard.js'
import IconMasterCard from './IconMasterCard.js'
import IconBoleto from './IconBoleto.js'

import { currencyOptions } from '../data/const.js'

class PaymentOption extends Component {
  render() {
    let {
        isSelected,
        name,
        saved,
        installments,
        totals,
        cardNumber,
        showEditOptions,
        selectedInstallment,
        removeSelectedPayment,
        modalOpen,
        setPaymentPage,
        intl: { formatNumber },
    } = this.props

    let brandIdentified = cardNumber && cardNumber.length > 3
    let lastNumbers = cardNumber && cardNumber.length === 16 && cardNumber.substr(cardNumber.length - 4)

    return (
      <div className={`ph5 pt4 c-on-base ${isSelected ? 'bg-muted-5 pb5 mh5' : ''}`}>
        <div className={`flex items-center`}>
          {
            !isSelected && (
              <div className="flex-none mr4 flex items-center justify-center bg-muted-5" style={{'width': '40px', 'height': '40px'}}>
                { name === 'CreditCard' && <Fragment>{ brandIdentified ? <IconMasterCard /> : <IconCard /> } </Fragment> }
                { name === 'Boleto' && <IconBoleto /> }
              </div>
            )
          }
          <div className="flex-auto fw5">
            <div className="flex">
                {
                isSelected && (
                  <div className="flex-none mr4 flex items-center justify-center">
                    { name === 'CreditCard' && <Fragment>{ brandIdentified ? <IconMasterCard /> : <IconCard /> } </Fragment> }
                    { name === 'Boleto' && <IconBoleto /> }
                  </div>
                )
              }
              <div className="flex-none">
                <FormattedMessage id={`checkout.payment${name}`}/>
              </div>
              { (saved || showEditOptions) && (
                <div className="ml3">
                  ∙∙∙∙ {lastNumbers}
                </div>
              ) }
            </div>
            {
              !isSelected && installments.length > 1 && (
                <div className="mt2 f6 fw5" style={{'color': '#79B03A'}}>
                  Em até {installments.length}x {installments[installments.length - 1] ? '' : 'sem juros'}
                </div>
              )
            }
            {
              showEditOptions && (
                <div className="mt2 f6 fw5">
                  {selectedInstallment}x {formatNumber(totals.total / selectedInstallment, currencyOptions)}
                </div>
              )
            }
          </div>
          <div className="flex-none ml1 mt0 self-start">
            { isSelected ? <IconEdit onClick={removeSelectedPayment} /> : <IconArrow/> }
          </div>
        </div>

        {
          showEditOptions && (
            <div className="flex mt3 f6 c-action-primary">
              {
                !saved && (
                  <div className="flex-none mr4 pv2" onClick={() => setPaymentPage('cardForm')}>
                    <FormattedMessage id="checkout.editCardData"/>
                  </div>
                )
              }
              <div className="flex-none pv2" onClick={() => modalOpen('installments')}>
                <FormattedMessage id="checkout.editInstallment"/>
              </div>
            </div>
          )
        }

        {!isSelected && <div className="mt5 bb b--muted-4"></div>}
    </div>
    )
  }
}

export default injectIntl(PaymentOption)
