import React, { Component, Fragment } from 'react'
import { FormattedMessage, injectIntl } from 'react-intl'

import GroupTitle from './GroupTitle'
import GenericItem from './GenericItem'

import { formatPostalCode } from '../utils.js'

class AddressList extends Component {
  render() {
    let {
      list,
      select,
    } = this.props

    return (
      <div>
        <div className="pt4 pb5">
          <div className="ph5 c-on-base">
            <GroupTitle><FormattedMessage id="checkout.newAddress"/></GroupTitle>
          </div>
          
          <GenericItem onClick={() => select(null)}>
            <FormattedMessage id="checkout.registerNewAddress"/>
          </GenericItem>
        </div>

        <div className="pb5">
          <div className="ph5 c-on-base">
            <GroupTitle><FormattedMessage id="checkout.savedAddresses"/></GroupTitle>
          </div>
          
          {
            list.map(item => {
              return (
                <Fragment>
                  <GenericItem onClick={() => select(item)}>
                    <div className="lh-title">
                      <div>{item.street}</div>
                      <div>{item.neighbourhood}</div>
                      <div>{item.city} - {item.state}</div>
                      <div>{formatPostalCode(item.location)}</div>
                    </div>
                  </GenericItem>
                </Fragment>
              )
            })
          }
        </div>
      </div>
    )
  }
}

export default injectIntl(AddressList)
