import React, { Component, Fragment } from 'react'
import { FormattedMessage, injectIntl } from 'react-intl'

import Button from '@vtex/styleguide/lib/Button'

class RemoveItems extends Component {
  handleConfirmRemoving = () => {
    this.props.removeRemainingItems()
    this.props.modalClose()
  }

  render() {
    let {
      remainingItems,
    } = this.props

    let number = remainingItems.length

    return (
      <Fragment>
        <div className="mb6">
          <FormattedMessage id={number === 1 ? 'checkout.confirmRemovingOne' : 'checkout.confirmRemovingMany'} values={{number: number}} />
        </div>
        <div>
          <Button variation="danger" size="small" onClick={this.handleConfirmRemoving}><FormattedMessage id="checkout.removeProducts"/></Button>
        </div>
      </Fragment>
    )
  }
}

export default injectIntl(RemoveItems)
