import React, { Component, Fragment } from 'react'
import { FormattedMessage, injectIntl } from 'react-intl'
import _ from 'lodash'
import Fade from 'react-reveal/Fade';

import Button from '@vtex/styleguide/lib/Button'
import Spinner from '@vtex/styleguide/lib/Spinner'

import ReviewBlock from './ReviewBlock.js'
import SlaOptionReview from './SlaOptionReview.js'
import PaymentOptionReview from './PaymentOptionReview.js'
import Totals from './Totals.js'
import GroupTitle from './GroupTitle.js'

import { formatPostalCode, formatDocument, formatPhone } from '../utils.js'

class StepReview extends Component {
  constructor() {
    super()
    this.state = {
      loading: false,
    }
  }

  handleConfirmPurchase = (e) => {
    e.preventDefault();
    this.setState({
			loading: true,
		})

    setTimeout(() => {
      this.setState({loading: false})
      this.props.goToNextStep()
    }, 2200)
  }

  handleShowInstallmentsAndConfirm = () => {
    this.props.enableShowSecurityAfterInstallment()
    this.props.modalOpen('installments')
  }

  render() {
    let {
      profile,
      address,
      location,
      selectedSla,
      selectedPayment,
      selectedInstallment,
      cardData,
      savedCardData,
      products,
      steps,
      setShowingItemsList,
      showPickup,
      formatData,
      logged,
      totals,
      modalOpen,
      neverChangedInstallments,
      returningShopper,
      intl: { formatMessage },
    } = this.props

    let shippingTitle = 'shipping'
    let hasShipping = true
    let hasPickup = true

    if (_.remove(JSON.parse(JSON.stringify(selectedSla)), sla => sla.channel === 'delivery').length === 0) {
      shippingTitle = 'pickupInPoint'
      hasShipping = false
    }
    if (_.remove(JSON.parse(JSON.stringify(selectedSla)), sla => sla.channel === 'pickup-in-point').length === 0) {
      shippingTitle = 'shipToHome'
      hasPickup = false
    }

    let hasSelectedDelivery = _.filter(selectedSla, (sla) => {
      return sla.channel === 'delivery'
    }).length > 0

    let selectedShippingSlas = _.filter(selectedSla, (sla) => {
      return sla.channel === 'delivery'
    })

    let selectedPickupSlas = _.filter(selectedSla, (sla) => {
      return sla.channel === 'pickup-in-point'
    })


    return (
      <div className="">
        <Fade cascade top duration={500} delay={400} distance="30%">
          <div className="mt5 ph4 pb5 lh-title">

            {
              steps.map((step, i) => {
                return (
                  <div>
                    {
                      step.name === 'profile' && (
                        <ReviewBlock logged={logged} goToStep={() => modalOpen(step.name)} title={formatMessage({id: `checkout.stepTitle${step.name}Review`})} step={step.name}>
                          <div className="mb2">{profile.name || "Nome Sobrenome"}</div>
                          <div className="">{profile.email || "email@exemplo.com"}</div>
                          <div>{profile.phoneNumber ? formatPhone(profile.phoneNumber) : "21 9988-3939"}<span className="ph3 c-muted-1">∙</span>{profile.document ? formatDocument(profile.document) : "088.347.009-12"}</div>
                        </ReviewBlock>
                      )
                    }
                    {
                      step.name === 'shipping' && (
                        <ReviewBlock goToStep={() => modalOpen(step.name)} title={formatMessage({id: `checkout.stepTitle${shippingTitle}`})} step={step.name}>

                          {
                            selectedPickupSlas.length > 0 && hasShipping && (
                              <GroupTitle review><FormattedMessage id="checkout.pickupInStore"/></GroupTitle>
                            )
                          }

                          {selectedPickupSlas.map((item, i) => {
                              return (
                                <div key={i} className={i < selectedPickupSlas.length - 1 ? 'mb3' : 'mb4'}>
                                  {
                                    <SlaOptionReview
                                      name={item.name}
                                      id={item.id}
                                      time={formatData(item.sla, item.timeType, item.channel)}
                                      channel={item.channel}
                                      price={item.lowPrice ? item.lowPrice : item.price}
                                      products={products}
                                      hasManySlasSelected={selectedSla.length > 1}
                                      availableItems={item.availableItems}
                                      selectedItems={item.selectedItems}
                                      setShowingItemsList={setShowingItemsList}
                                      showPickup={showPickup}
                                      pickup={item}
                                    />
                                  }
                                </div>
                              )
                          })}

                          {
                            selectedShippingSlas.length > 0 && hasPickup && (
                              <GroupTitle review><FormattedMessage id="checkout.shipToHome"/></GroupTitle>
                            )
                          }

                          {selectedShippingSlas.map((item, i) => {
                              return (
                                <div key={i} className={i < selectedShippingSlas.length - 1 ? 'mb3' : 'mb0'}>
                                  {
                                    <SlaOptionReview
                                      name={item.name}
                                      id={item.id}
                                      time={formatData(item.sla, item.timeType, item.channel)}
                                      channel={item.channel}
                                      price={item.lowPrice ? item.lowPrice : item.price}
                                      products={products}
                                      hasManySlasSelected={selectedSla.length > 1}
                                      availableItems={item.availableItems}
                                      selectedItems={item.selectedItems}
                                      setShowingItemsList={setShowingItemsList}
                                      showPickup={showPickup}
                                      pickup={item}
                                    />
                                  }
                                </div>
                              )
                          })}
                        </ReviewBlock>
                      )
                    }
                    {
                      step.name === 'address' && hasSelectedDelivery && (
                        <ReviewBlock goToStep={() => modalOpen(step.name)} title={formatMessage({id: `checkout.stepTitle${step.name}`})} step={step.name}>
                          {address.receiverName !== profile.name && <div className="mb2">{address.receiverName}</div>}
                          <div>{address.street}, {address.number ? address.number : 12}</div>
                          <div>{address.neighbourhood}</div>
                          <div>{address.city} - {address.state}</div>
                          <div>{formatPostalCode(location)}</div>
                        </ReviewBlock>
                      )
                    }
                    {
                      step.name === 'payment' && (
                        <ReviewBlock goToStep={() => modalOpen(step.name)} title={formatMessage({id: `checkout.stepTitle${step.name}`})} step={step.name}>
                          {
                            selectedPayment.length > 0 && (
                              <Fragment>
                                {selectedPayment.map((item, i) => {
                                    return (
                                      <div key={i} className={i < selectedPayment.length - 1 && 'mb6'}>
                                        {
                                          <PaymentOptionReview
                                            name={item.name}
                                            cardNumber={item.saved ? savedCardData.number.toString() : cardData.number.toString()}
                                            selectedInstallment={selectedInstallment}
                                            totals={totals}
                                            modalOpen={modalOpen}
                                            showInstallments={(!neverChangedInstallments && returningShopper) || !returningShopper}
                                          />
                                        }
                                      </div>
                                    )
                                })}
                              </Fragment>
                            )
                          }
                        </ReviewBlock>
                      )
                    }
                  </div>
                )
              })
            }
          </div>
        </Fade>

        <Totals
          selectedInstallment={selectedInstallment}
          selectedPayment={selectedPayment}
          selectedSla={selectedSla}
          totals={totals}
        />

        <div className="flex-none mt5 pa5 w-100">
          {
            selectedPayment.length > 0 && selectedPayment[0].saved ? (
              <Fragment>
                {
                  selectedPayment[0].installments.length > 1 && selectedInstallment === 1 && (
                    <div className="mb5">
                      <Button variation="tertiary" block onClick={this.handleShowInstallmentsAndConfirm}><FormattedMessage id={`checkout.selectInstallmentAndConfirm`}/></Button>
                    </div>
                  )
                }
                <div className="">
                  <Button size="large" block onClick={() => modalOpen('securityVerification')}>
                    { selectedInstallment === 1 ? <FormattedMessage id="checkout.confirmInSight" /> : <FormattedMessage id="checkout.confirmPurchase" /> }
                  </Button>
                </div>
              </Fragment>
            ) : (
              <Button size="large" block onClick={this.handleConfirmPurchase}>
                {this.state.loading ? <Spinner color="#fff" size={20} /> : <FormattedMessage id="checkout.confirmPurchase" />}
              </Button>
            )
          }
        </div>
      </div>
    )
  }
}

export default injectIntl(StepReview)
