import React, { Component } from 'react'
import { FormattedMessage, injectIntl } from 'react-intl'

import MainInstruction from './MainInstruction.js'
import PaymentOptionReview from './PaymentOptionReview.js'

import Button from '@vtex/styleguide/lib/Button'
import Input from '@vtex/styleguide/lib/Input'
import Spinner from '@vtex/styleguide/lib/Spinner'

class SecurityVerification extends Component {
  constructor() {
    super()
    this.state = {
      loading: false,
    }
  }

  confirmPurchase = (e) => {
    e.preventDefault();
    this.setState({
			loading: true,
		})

    setTimeout(() => {
      this.setState({loading: false})
      this.props.modalClose()
      this.props.goToNextStep()
      }, 2200)
  }

  render() {
    let {
      selectedPayment,
      cardData,
      totals,
      selectedInstallment,
      intl: { formatMessage }
    } = this.props

    return (
      <form className="ph5" onSubmit={this.confirmPurchase}>
        <div className="mb6">
          <MainInstruction><FormattedMessage id="checkout.securityInstructions"/></MainInstruction>
        </div>

        <PaymentOptionReview
          name={selectedPayment[0].name}
          cardNumber={cardData.number.toString()}
          selectedInstallment={selectedInstallment}
          totals={totals}
          showInstallments={true}
          pageModal="security"
        />

        <div className="mt6" style={{'width': '160px'}}>
          <Input type="tel" size="large" label={formatMessage({id: 'checkout.cvv'})} helpText={formatMessage({id: 'checkout.cvvHelp'})} />
        </div>

        <div className="mt6">
          <Button size="large" type="submit" block>
            {this.state.loading ? <Spinner color="#fff" size={20} /> : <FormattedMessage id="checkout.confirmPurchase" />}
          </Button>
        </div>
      </form>
    )
  }
}

export default injectIntl(SecurityVerification)
