import React, { Component, Fragment } from 'react'
import { injectIntl } from 'react-intl'

class ItemsPreview extends Component {
  render() {
    let {
      items,
      setShowingItemsList,
      name,
      time,
    } = this.props

    return (
      <div className="pt4 flex" onClick={() => setShowingItemsList({'name': name, 'time': time, 'items': items})}>
        {
          items.map((item, i) => (
            <Fragment key={i}>
              {
                (items.length <= 4 && i <= 3 || items.length > 4 && i <= 2) && (
                  <div className="flex-none mr4 relative">
                    <img alt={item.name} className="db br2" width="66" height="66" src={item.image}/>
                    {/* <QuantityIndicator quantity={item.quantity} unavailable={unavailable} /> */}
                    {/* { unavailable && <IconUnavailable /> } */}
                  </div>
                )
              }
            </Fragment>
          ))
        }
        {
          items.length > 4 && (
            <div className="flex-none ba br2 b--muted-3 c-action-primary ttu fw5 bg-white flex items-center tc" style={{height: '66px', width: '66px'}}>
              <div className="flex-auto pa2 f5">
                +{items.length - 3}
                {/* {items.length - 3 === 1 ? <FormattedMessage id="checkout.item"/> : <FormattedMessage id="checkout.items"/>} */}
              </div>
            </div>
          )
        }
      </div>
    )
  }
}

export default injectIntl(ItemsPreview)
