import React, { Component } from 'react'
import { FormattedMessage, injectIntl } from 'react-intl'

import IconArrow from './IconArrow.js'
import MainInstruction from './MainInstruction.js'

import { currencyOptions } from '../data/const.js'
import { calculateInterest } from '../utils.js'

class Installments extends Component {
  handleSelectInstallment = (item) => {
    this.props.setInstallment(item.number)
    this.props.setPaymentPage('')

    if (this.props.step === 'payment') {
      this.props.goToNextStep()
    }

    if (this.props.showSecurityAfterInstallment) {
      this.props.modalOpen('securityVerification')
    } else {
      this.props.modalClose()
    }

    this.props.disableNeverChangedInstallments()
  } 

  render() {
    let {
      installments,
      totals,
      intl: { formatNumber, }
    } = this.props

    return (
      <div className="">
        <div className="mh5 mb3">
          <MainInstruction><FormattedMessage id="checkout.selectAInstallment"/></MainInstruction>
        </div>

        {
          installments.length > 0 && (
            <div className="mt5">
              {
                installments.map((item, i) => {
                  return (      
                    <div
                      key={i}
                      className="flex-auto ph5 pt4 c-on-base"
                      onClick={() => this.handleSelectInstallment(item)}
                    >
                      <div className="flex items-center">
                        <div className="flex-auto fw5">
                          {
                            item.rate ? (
                              `${item.number}x ${formatNumber((calculateInterest(totals.subtotal + totals.shipping, item.number, item.rate)), currencyOptions)}`
                            ) : (
                              `${item.number}x ${formatNumber((totals.subtotal + totals.shipping) / item.number, currencyOptions)}`
                            )
                          }
                        </div>
                        {
                          !item.rate ? (
                            <div className="flex-none" style={{'color': '#79B03A'}}>
                              <FormattedMessage id="checkout.noInterest"/>
                            </div>
                          ) : (
                            <div className="flex-none c-muted-1">
                              Juros de {item.rate}% a.m.
                            </div>
                          )
                        }
                        <div className="flex-none ml1 mt0">
                          <IconArrow/>
                        </div>
                      </div>

                      <div className="mt5 bb b--muted-4"></div>
                    </div>
                  )
                })
              }
            </div>
          )
        }
      </div>
    )
  }
}

export default injectIntl(Installments)
