import React, { Component, Fragment } from 'react'
import { FormattedMessage, injectIntl } from 'react-intl'

import IconUnavailable from './IconUnavailable.js'
import QuantityIndicator from './QuantityIndicator.js'

class ItemsList extends Component {
  render() {
    let {
      options,
    } = this.props

    let quantity = 0

    if (options.items.length > 0) {
      options.items.forEach(item => {
        quantity += item.quantity
      })
    }

    let items = (
      <span className="c-muted-2 fw4"> ({quantity} {quantity === 1 ? <FormattedMessage id="checkout.item"/> : <FormattedMessage id="checkout.items"/>})</span>
    )

    return (
      <div className="mt4">
        <div className="ph5">
          {
            options.time || options.unavailable ?
            <Fragment>
              <div className="fw5 mb3 f4">{options.name ? options.name : <FormattedMessage id="checkout.remainingItems"/>}</div>
              <div className="fw4">{options.unavailable ? <FormattedMessage id="checkout.unavailablePlural"/> : options.time } {items}</div>
            </Fragment>
            :
            <Fragment>
              <div className="fw5 mb3">{options.name ? options.name : <FormattedMessage id="checkout.remainingItems"/>} {items}</div>
            </Fragment>
          }
        </div>
        <div className={`mt6 ph5 ${options.unavailable ? 'bg-warning--faded pv3' : ''}`}>
          {
            options.items.map((item, i) => {
              return(
                <div ley={i} className={`flex ${i < options.items.length - 1 ? 'mb5' : ''}`}>
                  <div className="flex-none mr4 relative">
                    <img alt={item.name} className="db br2" width="60" height="60" src={item.image}/>
                    <QuantityIndicator quantity={item.quantity} unavailable={options.unavailable} />
                    { options.unavailable && false && <IconUnavailable /> }
                  </div>
                  <div className="flex-auto flex items-center">
                    {item.name}
                  </div>
                </div>
              )
            })
          }
        </div>
      </div>
    )
  }
}

export default injectIntl(ItemsList)
