import React, { Component } from 'react'
import { injectIntl } from 'react-intl'

import IconArrow from './IconArrow'
import IconEdit from './IconEdit'

class GenericItem extends Component {
  render() {
    let {
      isSelected,
    } = this.props
  
    return (
      <div className={`flex-auto ph5 pt4 c-on-base ${isSelected && 'bg-muted-5 pb5'}`} onClick={!isSelected ? this.props.onClick : ''}>
        <div className="flex">
          <div className="flex-auto">{this.props.children}</div>

          <div className="flex-none ml1 mt0">
            { isSelected ? <IconEdit onClick={this.props.onClick} /> : <IconArrow/> }
          </div>
        </div>

        { !isSelected && <div className="mt5 bb b--muted-4"></div> }
      </div>
    )
  }
}

export default injectIntl(GenericItem)
