import React, { Component } from 'react'
import { FormattedMessage, injectIntl } from 'react-intl'

import Button from '@vtex/styleguide/lib/Button'
import Input from '@vtex/styleguide/lib/Input'
import Dropdown from '@vtex/styleguide/lib/Dropdown'
import Checkbox from '@vtex/styleguide/lib/Checkbox'
import ButtonPlain from '@vtex/styleguide/lib/ButtonPlain'

import { formatDocument } from '../utils.js'
class PaymentCardForm extends Component {
  constructor() {
    super()
    this.state = {
      number: '',
      name: '',
      dueMonth: '',
      dueYear: '',
      cvv: '',
      document: '',
      saveCard: false,
    }
  }

  componentDidMount() {
    this.setState({
      number: this.props.cardData.number,
      name: this.props.cardData.name,
      dueMonth: this.props.cardData.dueMonth,
      dueYear: this.props.cardData.dueYear,
      cvv: this.props.cardData.cvv,
      saveCard: this.props.cardData.saveCard,
      document: this.props.cardData.document,
		})
  }

  handleUpdateData = (key, value) =>{
    if (key === 'dueMonth' || key === 'dueYear') value = parseInt(value)
    if (key === 'saveCard') value = !this.state.saveCard
    if (key === 'number') this.props.changeTypedNumber(value)

    this.setState({
			[key]: value,
		})
  }

  handleSubmitData = (e) => {
    e && e.preventDefault();

    this.props.setCardData({
      name: this.state.name,
      number: this.state.number,
      dueMonth: this.state.dueMonth,
      dueYear: this.state.dueYear,
      cvv: this.state.cvv,
      saveCard: this.state.saveCard,
      document: this.state.document,
    })
  }

  handlePayInSight = (e) => {
    e && e.preventDefault();

    this.props.setInstallment(1)
    this.props.setPaymentPage('')

    this.handleSubmitData()
    if (this.props.step === 'review') {
      this.props.goToStep('review')
    } else {
      this.props.goToNextStep()
    }
  }

  handleOpenInstallments = () => {
    this.handleSubmitData()
    this.props.modalOpen('installments')
  }

  render() {
    let {
      address,
      installments,
      saved,
      intl: { formatMessage, },
    } = this.props

    let {
      number,
      name,
      dueMonth,
      dueYear,
      cvv,
      saveCard,
      document,
    } = this.state

    return (
      <form onSubmit={(e) => this.handlePayInSight(e)}>
        <div className={`${saved ? 'dn' : ''}`}>
          <div className="pa5 bg-muted-5 mt1 mh5">
            <div className="flex mb6">
              <div className="flex-none mr3">
                <svg width="14" height="16" viewBox="0 0 14 16" fill="none" xmlns="http://www.w3.org/2000/svg">
                  <path d="M7 0C4.8 0 3 1.8 3 4V5H1C0.4 5 0 5.4 0 6V15C0 15.6 0.4 16 1 16H13C13.6 16 14 15.6 14 15V6C14 5.4 13.6 5 13 5H11V4C11 1.8 9.2 0 7 0ZM8 11.7V13H6V11.7C5.4 11.4 5 10.7 5 10C5 8.9 5.9 8 7 8C8.1 8 9 8.9 9 10C9 10.7 8.6 11.4 8 11.7ZM9 5H5V4C5 2.9 5.9 2 7 2C8.1 2 9 2.9 9 4V5Z" fill="#979899"/>
                </svg>
              </div>
              <div className="flex-none fw6 c-muted-1">
                <FormattedMessage id={`checkout.secureData`}/>
              </div>
            </div>
            <div className="mb6">
              <Input type="tel" size="large" value={number} minLength="16" maxLength="16" label={formatMessage({id: 'checkout.number'})} onChange={(e) => { this.handleUpdateData('number', e.target.value)}} />
            </div>
            <div className="mb6">
              <Input size="large" value={name} label={formatMessage({id: 'checkout.nameAsInCard'})} onChange={(e) => { this.handleUpdateData('name', e.target.value)}} />
            </div>
            <div className="mb6 flex">
              <div className="flex-none" style={{'width': '110px'}}>
                <Dropdown
                  label={formatMessage({id: 'checkout.dueDate'})}
                  placeholder={formatMessage({id: 'checkout.month'})}
                  value={dueMonth}
                  size="large"
                  onChange={(e) => { this.handleUpdateData('dueMonth', e.target.value)}}
                  options={[
                    { value: 1, label: '01' },
                    { value: 2, label: '02' },
                    { value: 3, label: '03' },
                    { value: 4, label: '04' },
                    { value: 5, label: '05' },
                    { value: 6, label: '06' },
                    { value: 7, label: '07' },
                    { value: 8, label: '08' },
                    { value: 9, label: '09' },
                    { value: 10, label: '10' },
                    { value: 11, label: '11' },
                    { value: 12, label: '12' },
                  ]}
                />
              </div>
              <div className="flex-none mh3 pt7 mt3">
                /
              </div>
              <div className="flex-none" style={{'width': '110px'}}>
                <Dropdown
                  label="&nbsp;"
                  placeholder={formatMessage({id: 'checkout.year'})}
                  value={dueYear}
                  size="large"
                  onChange={(e) => { this.handleUpdateData('dueYear', e.target.value)}}
                  options={[
                    { value: 2021, label: '2021' },
                    { value: 2022, label: '2022' },
                    { value: 2023, label: '2023' },
                    { value: 2024, label: '2024' },
                    { value: 2025, label: '2025' },
                    { value: 2026, label: '2026' },
                    { value: 2027, label: '2027' },
                    { value: 2028, label: '2028' },
                    { value: 2029, label: '2029' },
                    { value: 2030, label: '2030' },
                    { value: 2031, label: '2031' },
                    { value: 2032, label: '2032' },
                  ]}
                />
              </div>
            </div>
            <div className="mb2" style={{'width': '160px'}}>
              <Input type="tel" size="large" value={cvv} minLength="3" maxLength="3" label={formatMessage({id: 'checkout.cvv'})} onChange={(e) => { this.handleUpdateData('cvv', e.target.value)}} helpText={formatMessage({id: 'checkout.cvvHelp'})} />
            </div>
          </div>

          <div className="ph5 pt5 mt3">
            <div className="mb5 lh-title dn">
              <FormattedMessage id="checkout.reviewExtraData"/>
            </div>
            <div className="mb6 w5 dn">
              <Input type="tel" size="large" value={document} minLength="11" maxLength="11" label={formatMessage({id: 'checkout.ownerDocument'})} onChange={(e) => { this.handleUpdateData('document', e.target.value)}} />
            </div>
            
            <div className="mb6">
              <div className="flex items-center fw5 mb4">
                <div className="flex-none mr3">
                  <FormattedMessage id="checkout.ownerDocument"/>
                </div>
                <div className="flex-none">
                  <ButtonPlain><span className="fw4"><FormattedMessage id="checkout.change"/></span></ButtonPlain>
                </div>
              </div>
              <div className="mb3">
                <div>{document && formatDocument(document)}</div>
              </div>
            </div>

            <div>
              <div className="flex items-center fw5 mb4">
                <div className="flex-none mr3">
                  <FormattedMessage id="checkout.billingAddress"/>
                </div>
                <div className="flex-none">
                  <ButtonPlain><span className="fw4"><FormattedMessage id="checkout.change"/></span></ButtonPlain>
                </div>
              </div>
              <div className="mb3">
                <div>{address.street}, {address.number ? address.number : 12}</div>
                <div>{address.neighbourhood}</div>
                <div>{address.city} - {address.state}</div>
              </div>
            </div>
          </div>

          <div className="pa5 dn">
            <Checkbox id="saveCard" size="large" checked={saveCard} label={formatMessage({id: 'checkout.savePaymentData'})} onChange={(e) => { this.handleUpdateData('saveCard', e.target.value)}} />
          </div>
        </div>


        <div className="flex-none pa5 mt5 w-100 bg-white">
          {
            installments.length > 1 && (
              <div className="mb5">
                <Button variation="secondary" block onClick={this.handleOpenInstallments}><FormattedMessage id={`checkout.selectInstallment`}/></Button>
              </div>
            )
          }
          <div className="">
            <Button type="submit" size="large" block><FormattedMessage id="checkout.payInSight" /></Button>
          </div>
        </div>
      </form>
    )
  }
}

export default injectIntl(PaymentCardForm)
