import React, { Component } from 'react'
import { FormattedMessage, injectIntl } from 'react-intl'

import { currencyOptions } from '../data/const.js'

class Totals extends Component {
render() {
    let {
      totals,
      // selectedSla,
      intl: { formatMessage, formatNumber }
    } = this.props

    // let shippingTitle = 'shipping'
    // if (selectedSla.length > 0) {
    //   if (_.remove(JSON.parse(JSON.stringify(selectedSla)), sla => sla.channel === 'delivery').length === 0) shippingTitle = 'pickupInPoint'
    //   if (_.remove(JSON.parse(JSON.stringify(selectedSla)), sla => sla.channel === 'pickup-in-point').length === 0) shippingTitle = 'shipToHome'
    // }

    // Shipping
    let totalShipping
    if (isNaN(totals.shipping)) {
      totalShipping = formatMessage({id: 'checkout.tbd'})
    } else {
      if (totals.shipping === 0) totalShipping = (<span className="ttu fw5" style={{'color': '#79B03A'}}>{formatMessage({id: 'checkout.free'})}</span>)
      else totalShipping = formatNumber(totals.shipping, currencyOptions)
    }

    return (
      <div className="ph5">
        <div className="flex">
          <div className="flex-none fw5"><FormattedMessage id="checkout.subtotal"/></div>
          <div className="flex-auto tr">{formatNumber(totals.subtotal, currencyOptions)}</div>
        </div>
        <div className="flex mt3">
          <div className="flex-none fw5"><FormattedMessage id={`checkout.totalsShipping`}/></div>
          <div className="flex-auto tr">{totalShipping}</div>
        </div>
        {
          totals.interest !== 0 && (
            <div className="flex mt3">
              <div className="flex-none fw5"><FormattedMessage id={`checkout.interests`}/></div>
              <div className="flex-auto tr">{formatNumber(totals.interest, currencyOptions)}</div>
            </div>
          )
        }
        <div className="flex f4 mt4">
          <div className="flex-none fw5"><FormattedMessage id="checkout.total"/></div>
          <div className="flex-auto tr">{formatNumber(totals.total, currencyOptions)}</div>
        </div>
      </div>
    )
  }
}

export default injectIntl(Totals)
