import React, { Component } from 'react'
import { FormattedMessage, injectIntl } from 'react-intl'

import IconMasterCard from './IconMasterCard.js'
import IconBoleto from './IconBoleto.js'

import { currencyOptions } from '../data/const.js'

class PaymentOptionReview extends Component {
  render() {
    let {
        name,
        selectedInstallment,
        totals,
        cardNumber,
        pageModal,
        modalOpen,
        showInstallments,
        intl: { formatNumber },
    } = this.props

    let lastNumbers = cardNumber && cardNumber.length === 16 && cardNumber.substr(cardNumber.length - 4)

    return (
      <div className="c-on-base">
        <div className="flex items-center">
          <div className="flex-auto fw5">
            <div className="flex">
              <div className="flex-none mr4 flex items-center justify-center">
                { name === 'CreditCard' && <IconMasterCard /> }
                { name === 'Boleto' && <IconBoleto /> }
              </div>
              <div className="flex-none">
                <FormattedMessage id={`checkout.payment${name}`}/>
              </div>
              { name === 'CreditCard' && (
                <div className="ml3">
                  ∙∙∙∙ {lastNumbers}
                </div>
              ) }
            </div>
            {
              (showInstallments && name === 'CreditCard') && (
                <div className="mt3 f6 fw5">
                  {selectedInstallment}x {formatNumber(totals.total / selectedInstallment, currencyOptions)}
                </div>
              )
            }
          </div>
        </div>
        {
          showInstallments && name === 'CreditCard' && (
            <div className="flexs mt2 f6 c-action-primary" onClick={() => modalOpen('installments')}>
              {
                !pageModal &&
                <div className="flex-none mr4 pv2">
                  <FormattedMessage id="checkout.editInstallment"/>
                </div>
              }
            </div>
          )
        }
    </div>
    )
  }
}

export default injectIntl(PaymentOptionReview)
