import React, { Component } from 'react'
import { FormattedMessage, injectIntl } from 'react-intl'

import Button from '@vtex/styleguide/lib/Button'
import Input from '@vtex/styleguide/lib/Input'
import Spinner from '@vtex/styleguide/lib/Spinner'

import EmailBullet from './EmailBullet.js'

class Identification extends Component {
  constructor() {
    super()

    this.state = {
      loading: false,
      email: '',
      name: '',
      phoneNumber: '',
      document: '',
    }
  }

  componentDidMount() {
    this.setState({
      email: this.props.profile.email,
      name: this.props.profile.name,
      phoneNumber: this.props.profile.phoneNumber,
      document: this.props.profile.document,
		})
  }

  handleUpdateData = (key, value) =>{
    this.setState({
			[key]: value,
		})
  }

  handleVerifyEmail = (e) => {
    e.preventDefault();
    this.setState({
			loading: true,
		})

    setTimeout(() => {
      this.setState({loading: false})

      this.props.setProfile({
        email: this.state.email,
      })

      this.props.modalClose()

      if (this.props.shortcutFlow) {
        this.props.goToStep('shipping')
      } else {
        this.props.goToNextStep()
      }
    }, 900)
  }

  handleResetData = () => {
    this.props.setProfile({
      email: '',
      name: '',
      phoneNumber: '',
      document: '',
    })
  }

  handleSubmitData = (e) => {
    e.preventDefault();

    this.props.setProfile({
      email: this.state.email,
      name: this.state.name,
      phoneNumber: this.state.phoneNumber,
      document: this.state.document,
    })
    
    this.props.goToNextStep()
  }

  render() {
    let {
      intl: { formatMessage, },
    } = this.props

    let {
      loading,
    } = this.state

    return (
      <form onSubmit={(e) => this.handleVerifyEmail(e)}>
        <div className="mt5 ph5">
          <div className="mb5">
            <Input type="email" size="large" label={formatMessage({id: 'checkout.email'})} onChange={(e) => { this.handleUpdateData('email', e.target.value)}} />
          </div>
          <div className="flex-none w-100 bg-white mb3">
            <Button type="submit" size="large" block>
              {loading ? <Spinner color="#fff" size={20} /> : <FormattedMessage id="checkout.verifyEmail" />}
            </Button>
          </div>
          <div className="mt7 bt b--muted-4 pt6 c-muted-1">
            <div className="mb4 fw6">
              <FormattedMessage id="checkout.youWillBeAbleTo" />
            </div>
            <EmailBullet><FormattedMessage id="checkout.useAddressAndCards" /></EmailBullet>
            <EmailBullet><FormattedMessage id="checkout.useRegisteredGiftCards" /></EmailBullet>
            <EmailBullet><FormattedMessage id="checkout.receiveNotifications" /></EmailBullet>
          </div>
        </div>
      </form>
    )
  }
}

export default injectIntl(Identification)
