import React, { Component, Fragment } from 'react'
import { FormattedMessage, injectIntl } from 'react-intl'

import ListItem from './ListItem.js'
import Totals from './Totals.js'

import Button from '@vtex/styleguide/lib/Button'

class Cart extends Component {
  constructor() {
    super()

		this.state = {
      typedAddress: '',
    }
  }

  handleVerifyEmail = () => {
    if (this.props.profile.email) {
      if (this.props.shortcutFlow) {
        this.props.goToStep('shipping')
      } else {
        this.props.goToNextStep()
      }
    } else {
      this.props.modalOpen('identification')
    }
  }

  render() {
    let {
      cartItems,
      selectedSla,
      totals,
      updateQuantity,
    } = this.props

    return (
      <Fragment>
        <div className="ph5 flex-auto">
          <div id="cart">
            <h3 className="t-heading-4 mb3 mt4"><FormattedMessage id="checkout.cart"/> <span className="ml2 f4 fw3 c-muted-1">({totals.numberOfItems} {totals.numberOfItems === 1 ? <FormattedMessage id="checkout.item"/> : <FormattedMessage id="checkout.items"/>})</span></h3>

            {cartItems.map((item, i) => {
              return (
                <ListItem
                  image={item.image}
                  key={i}
                  index={i}
                  name={item.name}
                  price={item.price}
                  quantity={item.quantity}
                  variations={item.variations}
                  updateQuantity={updateQuantity}
                />
              )
            })}
          </div>
        </div>
        
        <div className="mt6">
          <Totals
            selectedSla={selectedSla}
            totals={totals}
          />
        </div>
        
        <div className="mt4 flex-none pa5 w-100 bg-white">
          <Button size="large" block onClick={this.handleVerifyEmail}><FormattedMessage id="checkout.procede"/></Button>
        </div>
      </Fragment>

    )
  }
}

export default injectIntl(Cart)
